@media (min-width: 320px) {
  .main {
    display: flex;
    flex-direction: column;
  }
  .main__card {
    display: none;
    flex-direction: column;
    width: 240px;
    min-height: 545px;
    padding: 10px;
    border: 1px solid #e0e0e0;
    margin: 10px auto;
  }
}
@media (min-width: 768px) {
  .main__card {
    width: 600px;
    min-height: 250px;
    flex-direction: row;
  }
}
@media (min-width: 1200px) {
  .main__card {
    width: 750px;
  }
}
@media (min-width: 320px) {
  .product-info {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }
  .product-info__code-and-status {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin: 10px 0;
    order: 1;
  }
  .product-info__existence {
    cursor: pointer;
    color: #093;
    border-bottom: 1px dotted #093;
    font-size: 14px;
    line-height: 1.4;
  }
  .product-info__image-link {
    order: 2;
    width: 160px;
    height: 160px;
    margin: 0 auto;
    cursor: pointer;
  }
  .product-info__image {
    width: inherit;
    height: inherit;
  }
  .product-info__code {
    font-size: 12px;
    color: #666;
  }
  .product-info__name {
    margin: 70px 0 30px 0;
    order: 3;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    cursor: pointer;
  }
  .product-info__name:hover {
    text-decoration: underline;
  }
  .product-info__pay-for {
    display: flex;
  }
  .product-info__accessories {
    display: none;
    cursor: pointer;
  }
  .product-info__pay-for {
    display: flex;
    justify-content: space-evenly;
    order: 3;
    padding-left: 90px;
  }
  .product-info__price-club-card {
    order: 1;
    width: 190px;
    margin: 0 0 0 50px;
  }
  .product-info__card-price-text {
    font-weight: 400;
    font-size: 14px;
    display: inline-block;
    text-align: right;
    line-height: 17px;
  }
  .product-info__measure {
    text-align: center;
    color: #ffffff;
    background: #000000;
  }
  .product-info__gold-price {
    font-size: 28px;
    font-weight: 700;
    color: #000;
  }
  .product-info__price-default {
    order: 2;
    width: 125px;
    margin: 0 0 0 auto;
  }
  .product-info__retail-price {
    font-size: 26px;
    color: #a7a7a7;
  }
  .product-info__counter-plus-button {
    display: flex;
    order: 4;
    justify-content: space-between;
  }
  .product-info__price-points {
    display: none;
  }
  .product-info__sort {
    display: none;
  }
  .product-info__metre-price {
    color: #707070;
    border-bottom: 1px dotted #707070;
    font-size: 13px;
    line-height: 13px;
    cursor: pointer;
    padding: 5px 5px 5px 5px;
  }
  .product-info__package-price {
    color: #707070;
    border-bottom: 1px dotted #707070;
    font-size: 13px;
    line-height: 13px;
    cursor: pointer;
    padding: 5px 5px 5px 5px;
  }
  .product-info__button {
    width: 155px;
    background: #f90;
  }
}
@media (min-width: 768px) {
  .product-info:nth-of-type(1) {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .product-info__name {
    order: 0;
    margin: 0;
  }
  .product-info__code-and-status {
    width: 170px;
    order: 0;
    margin: 0 0 10px 0;
  }
  .product-info__image-link {
    order: 0;
    margin: 0;
  }
  .product-info__accessories {
    display: block;
    width: 150px;
    font-size: 12px;
    line-height: 14px;
  }
  .product-info__price-points {
    display: block;
    order: 3;
    font-size: 12px;
    line-height: 17px;
    color: #999;
  }
  .product-info__price-points {
    text-align: end;
  }
  .product-info__pay-for {
    order: 4;
  }
  .product-info__sort {
    display: block;
    order: 4;
    width: 220px;
    padding: 10px 0;
    border: 1px solid #a3cdde;
    text-align: center;
    position: relative;
  }
  .product-info__sort::before {
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    background: no-repeat url("../img/unit--i.png");
    background-size: cover;
    left: 10px;
  }
  .product-info__sort-text {
    display: block;
    font-size: 13px;
    line-height: 15px;
  }
}
@media (min-width: 1200px) {
  .product-info:nth-of-type(1) {
    display: grid;
    grid-template-columns: auto auto auto;
  }
  .product-info__accessories {
    height: max-content;
    grid-column-start: 2;
    grid-row-start: 2;
    margin: -111px 10px;
    width: unset;
  }
  .product-info__name {
    height: max-content;
    grid-column-start: 2;
    grid-row-start: 1;
    margin: 33px 0 0 10px;
  }
  .product-info__code-and-status {
    height: max-content;
    margin-left: 10px;
    grid-column-start: 2;
    grid-row-start: 1;
  }
  .product-info__gold-price {
    font-size: 25px;
  }
  .product-info__retail-price {
    font-size: 25px;
  }
  .product-info__metre-price {
    margin: 0;
  }
  .product-info__package-price {
    margin: 0;
  }
  .product-info__sort {
    margin: 0;
    width: 238px;
  }
  .product-info__price-club-card {
    text-align: end;
  }
  .product-info__price-default {
    text-align: end;
  }
}
@media (min-width: 320px) {
  .stepper {
    display: flex;
    width: 65px;
    min-height: 40px;
  }
  .stepper__arrow-wrapper {
    display: flex;
    flex-direction: column;
  }
  .stepper__up {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: no-repeat 100% 0px url(../../img/count--i.png);
  }
  .stepper__down {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: no-repeat url(../../img/count--i.png);
    background-position: right -18px;
  }
  .stepper__input {
    width: 40px;
    text-align: center;
    border: 1px solid #ccc;
  }
  .stepper__arrow {
    width: 25px;
    height: 20px;
    border: 1px solid #ccc;
    border-left: none;
    cursor: pointer;
  }
  .stepper__arrow:nth-of-type(2) {
    border-top: none;
  }
}
@media (min-width: 320px) {
  .button {
    display: flex;
    align-items: flex-end;
    justify-content: space-evenly;
    cursor: pointer;
  }
  .button__icon {
    fill: #ffffff;
  }
  .button__text {
    color: #ffffff;
    line-height: 40px;
    text-transform: uppercase;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .accessories__link {
    font-size: 12px;
    line-height: 14px;
    color: #666666;
  }
  .accessories__link:hover {
    text-decoration: underline;
  }
  .accessories__text {
    color: #666666;
    font-weight: 700;
  }
}
@media (min-width: 1200px) {
  .calculate__points {
    margin: 0;
  }
}
